/* General Container for the page */
.about-container {
    padding: 60px 20px;
    background: #000000;
    color: #fff;
    font-family: 'Roboto', sans-serif;
}

/* General Section Styling */
.about-section {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
    gap: 30px;
    flex-wrap: wrap; /* For responsive design */
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

.about-section.reverse {
    flex-direction: row-reverse; /* Reverses the image-text order for zig-zag */
}

.about-section:hover {
    transform: scale(1.02);
    box-shadow: 0 6px 25px rgba(237, 154, 20, 0.899);
}

/* Image Styling */
.about-image {
    flex: 0 0 300px;
    height: 400px;
    overflow: hidden;
    border-radius: 10px;
}

.about-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
}

.about-image img:hover {
    transform: scale(1.05);
}

/* Text Styling */
.about-text {
    flex: 1;
    max-width: 700px;
    padding: 20px;
    background-color: rgb(0, 0, 0);
    backdrop-filter: blur(10px);
    border-radius: 15px;
}

.about-text h2 {
    font-size: 2rem;
    color: rgb(255, 157, 0);
    margin-bottom: 20px;
    text-shadow: 0 0 10px rgba(255, 157, 0, 0.7), 0 0 20px rgba(255, 157, 0, 0.7);
}


.about-text p {
    font-size: 1rem;
    line-height: 1.6;
    color: #fff;
}

.about-text ul {
    list-style-type: disc;
    margin-left: 20px;
    font-size: 1.1rem;
}

.about-text ul li {
    margin-bottom: 10px;
}

.about-text ul li strong {
    color: rgb(255, 157, 0);
}

/* Centered Text */
.about-text.center {
    text-align: center;
}


/* Responsive Design */
@media (max-width: 768px) {
    .about-section {
        flex-direction: column;
        text-align: center;
    }

    .about-image {
        flex: 0 0 80%;
        height: 250px;
        margin-bottom: 20px;
    }

    .about-text {
        padding: 20px;
        width: 90%;
    }

    .about-text h2 {
        font-size: 1.5rem;
    }
}
