/* Overlay for the entire screen */
.register-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.7); /* Dark overlay background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.5s ease-in-out;
}

/* Form container styling */
.register-form {
  background: rgba(255, 255, 255, 0.2); /* Transparent background */
  backdrop-filter: blur(12px); /* Glassmorphism effect */
  border-radius: 12px;
  padding: 40px;
  width: 350px;
  box-shadow: 0 12px 60px rgba(0, 0, 0, 0.3); /* Depth and shadow */
  text-align: center;
  color: white;
  position: relative;
  animation: slideIn 0.5s ease-out;
}

/* Title styling */
.register-form h2 {
  font-size: 28px;
  font-weight: 600;
  margin-top: 20px;
  margin-bottom: 30px;
  color: white;
  text-shadow: 0 0 10px rgba(255, 165, 0, 0.8), 0 0 20px rgba(255, 165, 0, 0.5);
  animation: glow 1.5s infinite alternate;
}

/* Input field styling */
.register-form input {
  display: block;
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border-radius: 6px;
  border: none;
  background-color: rgba(94, 86, 86, 0.152);
  color: #333333; /* Dark grey text color */
  font-size: 16px;
  background-color: rgba(255, 255, 255, 0.6);
  transition: all 0.3s ease;
}

/* Ensuring the password input field has the same style with white border */
.register-form input[type="password"] {
  display: block;
  width: 100%;
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 6px;
  height: 45px;
  border: 2px solid white; /* White border */
  background-color: rgba(46, 43, 43, 0.795);
  color: #fbf7f7; /* Dark grey text color */
  font-size: 16px;
  background-color: rgb(29, 27, 27);
  transition: all 0.3s ease;
}

/* Submit button styling */
.register-form button[type="submit"] {
  padding: 15px;
  width: 100%;
  background-color: orange;
  color: white;
  font-size: 18px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease, box-shadow 0.3s ease;
  box-shadow: 0 4px 12px rgba(255, 165, 0, 0.5);
}

/* Hover effect for submit button */
.register-form button[type="submit"]:hover {
  background-color: #ff7f00;
  box-shadow: 0 8px 20px rgba(255, 165, 0, 0.8);
}

/* Close button styling */
.register-form .close-btn {
  background-color: black;
  color: white;
  margin-top: 0px;
  padding: 8px;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 40px;
}

/* Hover effect for close button */
.register-form .close-btn:hover {
  background-color: #333333;
}

/* Animations for effects */
@keyframes glow {
  0% {
    text-shadow: 0 0 10px rgba(255, 165, 0, 0.8), 0 0 20px rgba(255, 165, 0, 0.5);
  }
  100% {
    text-shadow: 0 0 30px rgba(255, 165, 0, 1), 0 0 40px rgba(255, 165, 0, 0.8);
  }
}

@keyframes slideIn {
  from {
    transform: translateY(-50px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}


