/* Profile container styling */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
}

.profile-page {
  display: flex;
  position: relative;
  margin-top: 0px;
}

.sidebar {
  width: 250px;
  height: calc(100vh - 70px);
  background-color: #111;
  position: fixed;
  top: 92px;
  left: -250px;
  transition: left 0.3s ease;
  padding-top: 80px;
  overflow-y: auto;
  z-index: 1;
  box-shadow: 4px 0px 10px rgba(0, 0, 0, 0.5);
  visibility: visible;
}

.sidebar.open {
  left: 0;
}

.sidebar ul {
  list-style-type: none;
  padding: 0;
}

.sidebar ul li {
  padding: 20px;
  text-align: center;
  font-size: 18px;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

.sidebar ul li:hover {
  background-color: #575757;
}

.toggle-btn {
  position: fixed;
  top: calc(50% + 35px);
  left: 0;
  background-color: #111;
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  font-size: 20px;
  border-radius: 0 5px 5px 0;
  transition: left 0.3s ease;
  z-index: 2;
}

.sidebar.open .toggle-btn {
  left: 250px;
}

/* Profile content styling */
.profile-content {
  margin-left: 250px;
  padding: 20px;
  flex-grow: 1;
  background-color: #fff;
  height: calc(100vh - 70px);
  transition: margin-left 0.3s ease;
}

.sidebar:not(.open) ~ .profile-content {
  margin-left: 0;
}

/* Profile header styling */
.profile-header {
  text-align: center;
  margin-bottom: 10px; /* Reduced margin to bring content closer */
}

.profile-pic {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto;
  margin-bottom: 0px; /* Reduced margin between image and the next element */
}

.profile-pic img {
  width: 90%;
  height: 90%;
  object-fit: cover;
}


h3 {
  font-size: 24px;
  color: #333;
  margin: 0px ; /* Reduced gap to bring it closer to the profile image */
  margin-bottom: 20px;
  text-align: center;
  padding: 1px;
  background-color: transparent; /* Removed yellow background */
}

/* Content section with flexbox for images */
.content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.programs {
  display: flex;
  justify-content: space-between; /* Space between images */
  width: 90%;
  margin-left: 20px;
  gap: 100px; /* Added gap between images */
}

.program-img {
  width: 300px;
  height: 380px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}

.program-img:hover {
  transform: scale(1.05);
}

.show-programs .programs {
  display: flex;
}

.form-container {
  background: rgba(0, 0, 0, 0.5);
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.3);
  color: white;
  width: 400px;
  margin: 0 auto;
  position: relative;
}

input[type="text"], input[type="email"], input[type="date"], input[type="number"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 5px;
  border: 2px solid #fff;
  background-color: #222;
  color: #fff;
}

input[type="text"]:focus, input[type="email"]:focus, input[type="date"]:focus, input[type="number"]:focus {
  border-color: #ff6f00;
}

button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

button.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #ff6f00;
  color: white;
}

button.submit-btn {
  background-color: #111;
  color: white;
  position: absolute;
  bottom: 10px;
  right: 10px;
}

button.close-btn:hover, button.submit-btn:hover {
  background-color: #575757;
}

/* Media Queries for Smaller Screens */
@media (max-width: 768px) {
  .sidebar {
    width: 200px;
  }

  .profile-content {
    margin-left: 0;
  }

  .program-img {
    width: 80%;
    margin-left: 30px;
  }

  /* Adjust gap between images on smaller screens */
  .programs {
    gap: 30px; /* Reduce gap on smaller screens */
  }
}
