/* Container for Stories Section */
.stories-container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 10px;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
  color: #333;
  margin-bottom: 30px;
}

/* Stories Tile Grid */
.stories-tiles {
  display: grid;
  grid-template-columns: repeat(3, 1fr); /* 3 tiles per row */
  gap: 60px;
}

/* Individual Story Tiles */
.story-tile {
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 5px;
  transition: transform 0.3s ease;
  cursor: pointer;
  text-align: center;
}

.story-tile:hover {
  transform: translateY(-5px);
}

/* Story Image */
.story-image {
  width: 280px;
  height: 220px;
  border-radius: 10px;
  margin-bottom: 10px;
  margin-top: 15px;
}

.story-tile h3 {
  font-size: 20px;
  color: #333;
  margin-bottom: 5px;
}

.story-date {
  font-size: 14px;
  color: #777;
  margin-bottom: 15px;
}

.story-tile p {
  font-size: 13px;
  color: #555;
  line-height: 1.1;
}

/* Comments Section */
.comments-section {
  margin-top: 20px;
}

.comments-section h4 {
  font-size: 14px;
  color: #333;
  margin-bottom: 10px;
}

.comments-list {
  list-style-type: none;
  padding-left: 0;
}

.comments-list li {
  background-color: #f9f9f9;
  padding: 2px;
  border-radius: 5px;
  margin-bottom: 2px;
  font-size: 14px;
  color: #555;}

/* Input Field and Button for Comments */
.comment-input-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 50px;
  margin-top: 2px;
  width: 300px;
}

.comment-input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 11px;
  box-sizing: border-box;
  margin-bottom: 10px; /* Add margin to separate input from the button */
}

.comment-btn {
  width: 50%; /* Set width to 50% of the container */
  padding: 10px;
  background-color: #ff6f20;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
  text-align: center;
}

.comment-btn:hover {
  background-color: #e65b1f;
}

/* Mobile Responsiveness */
@media (max-width: 768px) {
  .stories-tiles {
      grid-template-columns: repeat(1, 1fr); /* 1 column on smaller screens */
  }

  .comment-input-container {
      width: 100%;
  }

  .comment-input {
      width: 100%;
  }

  .comment-btn {
      width: 100%;
  }
}

