/* Background for the entire category section */
.category-section {
  margin-bottom: 0px;
  background-color: rgb(255, 132, 0); /* Orange background for the section */
  padding: 0px;
}

/* Styling for the category title */
.category-title {
  font-size: 24px;
  font-weight: bold;
  color: #fff; /* White text color */
  text-align: center;
  padding-top: 80px;
  padding-bottom: 0px;
}

/* Carousel container */
.category-carousel {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 71vh; /* Height of the carousel */
  background-color: rgb(255, 132, 0); /* Orange background for the carousel */
}

/* Carousel content for sliding images */
.carousel-content {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: transform 0.3s ease-in-out; /* Smooth transition for slides */
  margin-top: 0px;
}

/* Row of images within the carousel */
.category-row {
  display: flex;
  justify-content: space-between;
  width: 90%;
  gap: 20px;
}

/* Individual category item */
.category-item {
  width: 25%;
  text-align: center;
  position: relative;
  border-radius: 10px; /* Rounded corners */
  padding-right: 80px; /* Padding inside the items */
  display: flex;
  flex-direction: column;
  align-items: center; /* Center content inside the item */
}

/* Category image styling */
.category-image {
  width: 280px;
  height: 300px; /* Ensures image fills the container */
  border-radius: 10px;
  margin-bottom: 20px; /* Adjust space between image and text */
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

/* Hover effect for images */
.category-item:hover .category-image {
  transform: scale(1.05); /* Slight zoom on hover */
  box-shadow: 0 0 50px rgb(6, 6, 6); /* Glowing effect */
}

/* Category text and description styling */
.category-item h3 {
  font-size: 18px; /* Heading font size */
  color: #0c0b0b; /* Dark color for heading */
  margin-bottom: 10px; /* Space between heading and description */
}

.category-item p {
  font-size: 14px; /* Description font size */
  color: #050505; /* Slightly lighter color for description */
  text-align: center; /* Ensure text is centered under the heading */
}

/* Button styling */
.carousel-btn {
  background-color: rgba(0, 0, 0, 0.5);
  color: #030303;
  font-size: 24px;
  padding: 10px;
  border: none;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  cursor: pointer;
  z-index: 1;
  border-radius: 5px;
  transition: background-color 0.3s;
}
.enroll-btn {
  background-color: black;
  color: orange;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
  margin-top: 10px;
}

.enroll-btn:hover {
  background-color: rgb(75, 73, 69);
  color: rgb(253, 252, 250);
}

.carousel-btn:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

/* Left and right buttons */
.prev-btn {
  left: 10px;
}

.next-btn {
  right: 10px;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); /* Semi-transparent black background */
  z-index: 1000; /* Ensure it's above other content */
  display: flex;
  justify-content: center;
  align-items: center;
}


