/* Profile container styling */
.profile-container {
    width: 450px;
    margin: 0 auto;
    margin-top: 0px;
    padding: 30px;
    background: rgb(3, 2, 2);
    backdrop-filter: blur(10px);
    border-radius: 15px;
    box-shadow: 0 4px 30px rgba(157, 149, 149, 0.1);
    text-align: center;
    color: #fff;
}

/* Profile details styling */
.profile-details {
    margin-bottom: 5px;
}

.profile-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 10px 0; /* Increased margin to ensure uniform gaps */
    font-size: 1.2rem;
}

.profile-item strong {
    font-weight: bold;
    color: rgb(255, 157, 0);
    text-align: left;
    width: 40%;
    margin-left: 20px;
}

.profile-item span {
    color: rgb(255, 255, 255);
    padding: 5px 5px;
    border-radius: 5px;
    width: 55%;
    text-align: left;
}

h2{
    color: rgb(255, 255, 255);
    margin-top: 0px;
    margin-bottom: 3px;
    align-self: center;
}

.profile-item input {
    padding: 5px;
    width: 55%;
    background-color: rgb(3, 2, 2);
    color: #fff;
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 5px;
    font-size: 1rem;
    margin-left: 15px;
    margin-bottom: 10px; /* Added bottom margin to make gaps uniform */
}

.profile-item input:focus {
    border-color: orange;
    outline: none;
}

/* Button styles */
.edit-btn{
    padding: 10px 20px;
    margin-top: 15px;
    margin-bottom: 5px;
    background: linear-gradient(135deg, rgb(255, 149, 0), darkorange);
    color: white;
    border: none;
    border-radius: 25px;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}
.save-btn {
    padding: 10px 20px;
    margin-top: 0px;
    margin-bottom: 0px;
    background: linear-gradient(135deg, rgb(255, 149, 0), darkorange);
    color: white;
    border: none;
    border-radius: 25px;
    font-size: 1rem;
    font-weight: bold;
    cursor: pointer;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.close-btn {
    padding: 8px;
    width: 40px;
}

.edit-btn:hover, .save-btn:hover, .close-btn:hover {
    transform: scale(1.05);
    box-shadow: 0 6px 15px rgba(239, 125, 4, 0.5);
}

/* Success message styling */
.success-message {
    margin-top: 10px;
    font-size: 1.1rem;
    color: lightgreen;
    font-weight: bold;
    animation: fadeIn 0.5s ease-in-out;
}

/* Fade-in effect for success message */
@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

/* Responsive design for smaller screens */
@media (max-width: 768px) {
    .profile-container {
        width: 90%;
    }

    .profile-item {
        font-size: 1rem;
    }

    .profile-item input {
        width: 70%; /* Adjusting input width on smaller screens */
    }
}

  
  