/* src/components/Navbar.css */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: black;
  padding: 10px 20px;
  color: white;
}

.navbar-brand {
  display: flex;
  align-items: center;
}

.logo {
  width: 100px; /* Adjust width as needed */
  height: 5x;
  margin-right: 10px;
}

.navbar-links {
  display: flex;
  gap: 30px;
}

.navbar-actions {
  display: flex;
  align-items: center;
  gap: 30px;
}

.profile-icon {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.signup-button, .login-button {
  background-color: orange;
  color: black;
  border: none;
  padding: 10px 15px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.signup-button:hover, .login-button:hover {
  background-color: #ff7f00;
}

.navbar a {
  color: white;
  text-decoration: none;
  font-size: 1.2rem;
}

.navbar a:hover {
  color: orange;
  transition: color 0.3s ease-in-out;
}
